export const showToast = (message) => {
    console.log(message)
    const toast = document.createElement("span");
    toast.innerText = message;
    toast.classList.add("toast");
    const toastWraper = document.getElementById("toast-wrapper");
    toastWraper.appendChild(toast);
    setTimeout(() => {toastWraper.removeChild(toast)}, 3000);
}